  transform: translateY(-2px);
}

button:active {
  box-shadow: #3c4fe0 0 3px 7px inset;
  transform: translateY(2px);
}

.main-container {
    height: 77%;
    width: 100%;
    background-color: rgba(0, 0, 0, .8);
    display: grid;
    grid-template-columns: 5% 95%;
    grid-template-rows: 8% 5% 5% 55% 1.7% 25%;
}

.turn-announcement-container {
    grid-area: 1 / 1 / 2 / 3;
    text-align: center;
    font-weight: bold;
}

.turn-announcement-container h2 {
    font-size: 2rem;
}

.board-info-container {
    display: flex;
