play: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    height: 100%;\n    gap: 10%;\n}\n\n.player1-menu-container, .player2-menu-container {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: .5rem;\n}\n\n.label {\n    font-weight: bold;\n    font-size: 1.3rem;\n    color: paleturquoise;\n}\n\n.start-menu-form p {\n    color: paleturquoise;\n}\n\n.start-menu-button-container {\n    display: flex;\n    justify-content: center;\n    gap: 15%;\n}\n\n.start-menu-button-container div {\n    display: flex;\n    flex-direction: column;\n    width: 25%;\n    align-items: center;\n    text-align: center;\n    gap: .5rem;\n}\n\n.start-menu-form input {\n    border-radius: .5rem;\n    padding: .2rem;\n}\n\n.instructions-container {\n    position: absolute;\n    top: 5%;\n    left: 5%;\n    width: 90%;\n    z-index: 3;\n    background-color: darkblue;\n    color: paleturquoise;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 1rem;\n    border: 1px solid black;\n    box-shadow: .5rem .5rem 1rem black;\n    padding-bottom: 2rem;\n}\n\n.instructions-player-name {\n    font-size: 2rem;\n    margin-top: 1rem;\n}\n\n.instructions-container h1 {\n    font-weight: bold;\n    font-size: 2.5rem;\n}\n\n.available-ships-container {\n    background-color: darkblue;\n    position: absolute;\n    width: 90%;\n    height: 23%;\n    top: 74%;\n    left: 2.5%;\n    border: 1px solid black;\n    box-shadow: .5rem .5rem 1rem black;\n    display: grid;\n    grid-template-columns: 1fr auto;\n    grid-template-rows: auto 1fr;\n    padding: .5rem;\n}\n\n.place-ship-text {\n    grid-area: 1 / 1 / 2 / 3;\n    font-weight: bold;\n    font-size: 1.5rem;\n    text-align: center;\n}\n\n.ship-image-frame {\n    grid-area: 2 / 1 / 3 / 2;\n    background-color: rgba(255, 255, 255, .2);\n    border: 2px solid black;\n    min-height: fit-content;\n    max-height: 9rem;\n    min-width: fit-content;\n    max-width: 9rem;\n    box-shadow: .2rem .2rem 1rem black;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    padding: .5rem;\n}\n\n.rotate-ship-button {\n    align-self: center;\n    justify-self: center;\n    margin-right: 2rem;\n}\n\n.gameover-container {\n    position: absolute;\n    z-index: 3;\n    height: 50%;\n    width: 90%;\n    left: 5%;\n    top: 20%;\n    background-color: darkblue;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 10%;\n    border: 1px black solid;\n}\n\n.gameover-text {\n    font-weight: bold;\n    font-size: 2.3rem;\n    color: paleturquoise;\n}\n\nbutton {\n  align-items: center;\n  appearance: none;\n  background-image: radial-gradient(100% 100% at 100% 0, #5adaff 0, #5468ff 100%);\n  border: 0;\n  border-radius: 6px;\n  box-shadow: rgba(45, 35, 66, .4) 0 2px 4px,rgba(45, 35, 66, .3) 0 7px 13px -3px,rgba(58, 65, 111, .5) 0 -3px 0 inset;\n  box-sizing: border-box;\n  color: #fff;\n  cursor: pointer;\n  display: inline-flex;\n  height: 48px;\n  justify-content: center;\n  line-height: 1;\n  list-style: none;\n  overflow: hidden;\n  padding-left: 16px;\n  padding-right: 16px;\n  position: relative;\n  text-align: left;\n  text-decoration: none;\n  transition: box-shadow .15s,transform .15s;\n  user-select: none;\n  -webkit-user-select: none;\n  touch-action: manipulation;\n  white-space: nowrap;\n  will-change: box-shadow,transform;\n  font-size: 18px;\n}\n\nbutton:focus {\n  box-shadow: #3c4fe0 0 0 0 1.5px inset, rgba(45, 35, 66, .4) 0 2px 4px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;\n}\n\nbutton:hover {\n  box-shadow: rgba(45, 35, 66, .4) 0 4px 8px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;\n  transform: translateY(-2px);\n}\n\nbutton:active {\n  box-shadow: #3c4fe0 0 3px 7px inset;\n  transform: translateY(2px);\n}\n\n.main-container {\n    height: 77%;\n    width: 100%;\n    background-color: rgba(0, 0, 0, .8);\n    display: grid;\n    grid-template-columns: 5% 95%;\n    grid-template-rows: 8% 5% 5% 55% 1.7% 25%;\n}\n\n.turn-announcement-container {\n    grid-area: 1 / 1 / 2 / 3;\n    text-align: center;\n    font-weight: bold;\n}\n\n.turn-announcement-container h2 {\n    font-size: 2rem;\n}\n\n.board-info-container {\n    display: flex;\n    grid-area: 2 / 1 / 3 / 3;\n    align-items: end;\n    justify-content: center;\n    font-weight: bold;\n}\n\n.grid-container {\n    align-self: center;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    grid-area: 4 / 2 / 5 / 3;\n    overflow: hidden;\n}\n\n.column-labels-container {\n    font-weight: bold;\n    display: grid;\n    grid-template-columns: repeat(10, 2rem);\n    grid-area: 3 / 2 / 4 / 3;\n    align-items: end;\n    justify-content: center;\n    text-align: center;\n    gap: .1rem;\n}\n\n.row-labels-container {\n    align-self: center;\n    font-weight: bold;\n    display: grid;\n    grid-area: 4 / 1 / 5 / 2;\n    align-items: center;\n    justify-content: end;\n    text-align: center;\n    grid-template-rows: repeat(10, 2rem);\n    gap: .15rem;\n}\n\n.grid {\n    background-image: url(\"./water.jpg\");\n    background-size: contain;\n    display: grid;\n    height: min-content;\n    width: min-content;\n    grid-template-columns: repeat(10, 1fr);\n    grid-template-rows: auto;\n    gap: 0;\n    align-items: center;\n    justify-content: center;\n}\n\n.cell {\n    height: 2rem;\n    width: 2rem;\n    border: black solid 1px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n.Battleship {\n    position: relative;\n    left: 3.3rem;\n    width: 8rem;\n}\n\n.Carrier {\n    position: relative;\n    left: 4.3rem;\n    width: 10rem;\n}\n\n.Cruiser {\n    position: relative;\n    left: 2.2rem;\n    width: 6rem;\n}\n\n.Destroyer {\n    width: 4rem;\n    position: relative;\n    left: 1rem;\n}\n\n.Submarine {\n    z-index: 1;\n    position: relative;\n    left: 2rem;\n    width: 6rem;\n}\n\n.vertical .Battleship {\n    left: 0;\n    top: 3.3rem;\n}\n\n.vertical .Carrier {\n    left: 0;\n    top: 4.4rem;\n}\n\n.vertical .Cruiser {\n    left: 0;\n    top: 2.2rem;\n}\n\n.vertical .Destroyer {\n    left: 0;\n    top: 1.1rem;\n}\n\n.vertical .Submarine {\n    left: 0;\n    top: 2.2rem;\n}\n\n.mini-ship.Battleship {\n    left: calc(var(--mini-cell-size) * 1.7);\n    top: calc(var(--mini-cell-size) * .01);\n    width: calc(var(--mini-cell-size) * 4.2);\n}\n\n.mini-ship.Carrier {\n    top: -.2rem;\n    left: calc(var(--mini-cell-size) * 2.3);\n    width: calc(var(--mini-cell-size) * 5.5);\n}\n\n.mini-ship.Cruiser {\n    top: -.2rem;\n    left: calc(var(--mini-cell-size) * 1.2);\n    width: calc(var(--mini-cell-size) * 3.2);\n}\n\n.mini-ship.Destroyer {\n    top: -.1rem;\n    left: calc(var(--mini-cell-size) * .65);\n    width: calc(var(--mini-cell-size) * 2.2);\n    height: 1rem;\n    \n}\n\n.mini-ship.Submarine {\n    left: calc(var(--mini-cell-size) * 1.1);\n    top: -.1rem;\n    width: calc(var(--mini-cell-size) * 3.2);\n}\n\n.mini-ship.Battleship.vertical {\n    left: calc(var(--mini-cell-size) * .03);\n    top: calc(var(--mini-cell-size) * 1.6);\n}\n\n.mini-ship.Carrier.vertical {\n    left: calc(var(--mini-cell-size) * -.05);\n    top: calc(var(--mini-cell-size) * 2.25);\n}\n\n.mini-ship.Cruiser.vertical {\n    left: calc(var(--mini-cell-size) * .05);\n    top: 1.1rem;\n}\n\n.mini-ship.Destroyer.vertical {\n    left: calc(var(--mini-cell-size) * .1);\n    top: calc(var(--mini-cell-size) * .55);\n}\n\n.mini-ship.Submarine.vertical {\n    left: calc(var(--mini-cell-size) * -.05);\n    top: calc(var(--mini-cell-size) * 1.1);\n}\n\n.cell:hover {\n    border: darkblue solid 1px;\n}\n\n.error-message {\n    display: flex;\n    grid-area: 6 / 1 / 7 / 3;\n    color: red;\n    justify-content: center;\n    align-self: end;\n    font-size: .85rem;\n}\n\n.sunk-sh